@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
body{font-family:"Mulish", sans-serif}.navbar-light .navbar-brand{font-size:30px;color:#8a2be2 !important}.navbar .active{font-weight:bold;border-bottom:1px solid #8a2be2;color:#8a2be2 !important}.brand-name{color:#8a2be2}.get-Started .btn{border-radius:30px}.get-Started .btn-primary{background-color:#fff;border-color:#8a2be2;color:#8a2be2;transition:0.5s}.get-Started .btn-primary:hover{background-color:#8a2be2;border-color:#8a2be2}.desc_style{font-size:20px}.animated{animation:up-down 2s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}.card-img-top{height:200px}footer{position:fixed;bottom:0;height:40px;padding:5px}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

